/* Base Styles for the A2 Container */
.a2-container {
  width: 100vw;
  min-height: 100vh;
  background-color: rgb(255, 255, 255);
}

/* Rectangle Divider: Full-width at the top of the viewport */
.a2-divider {
    width: 100vw; /* Full width of the viewport */
    height: 8vh; /* Height set to 8% of the viewport height */
    background-color: #E8E8E8; /* Light grey color */
    position: absolute;
    top: 0; /* Positioned at the top */
    left: 0; /* Aligned to the left edge */
  }
  
